@import "@/assets/styles/_variables.scss";

    .char {
        &__extraStats {
            @include content;

            padding-top: 12px;
            padding-bottom: 12px;

            &__stat {
                border-radius: 4px;
                margin-bottom: 8px;
                border: 1px solid $gray;

                &-initial{
                    padding: 8px 12px;
                    display: flex;
                    justify-content: space-between;
                    background-color: transparentize($gray, .75);
                }

                &-title {
                    font-weight: 600;
                }
            }

            &__item {
                border-top: 1px solid $gray;
                display: flex;
                justify-content: space-between;
                width: 100%;
                padding: 8px 12px;

                &:last-child {
                    border-bottom: 0;
                }

                &-value {
                    font-weight: 600;
                }
            }
        }
    }
